import { useLoaderData } from "remix"
import Box from "~/components/BaseKit/Box/Box"
import { ColumnCards, links as ColumnCardsLinks } from "~/components/ColumnCards/ColumnCards"
import { LeftNavbar, links as LeftNavbarlinks } from "~/components/Navigation/LeftNavbar/LeftNavbar"
import { Title, links as TitleLinks } from "~/components/Title/Title"
import { getCardsOnBoard, getListsOnBoard, getMembersOnBoard } from "~/services/trello"
import { members } from "../services/trellotypes"
import { authenticator } from "~/services/auth"

export function links() {
  return [...LeftNavbarlinks, ...TitleLinks, ...ColumnCardsLinks]
}

const factorSearch = (data, factor) => {
  return data.labels.find((ee) => ee.name === factor)
}
const extractDeadLine = (data) => {
  return (
    data
      ?.split("\n")
      ?.find((e) => e?.indexOf("**PREVISÃO:**") > -1)
      ?.split("**PREVISÃO:**")
      ?.slice(1)?.[0]
      ?.replace(" ", "") || "Não Definido"
  )
}
const extractCreatedAt = (data) => {
  return (
    data
      ?.split("\n")
      ?.find((e) => e?.indexOf("**DATA DE CRIAÇÃO:**") > -1)
      ?.split("**DATA DE CRIAÇÃO:**")
      ?.slice(1)?.[0]
      ?.replace(" ", "") || "Não Definido"
  )
}
export let loader = async ({ request }) => {
  await authenticator.isAuthenticated(request, {
    failureRedirect: "/logar",
  })

  let cards = await getCardsOnBoard()
  let boards = await getListsOnBoard()
  // let members = await getMembersOnBoard();

  let group = [
    {
      title: "IMPLEMENTAÇÃO",
      data: [],
      emoji: "💡",
    },
    {
      title: "CORREÇÕES",
      data: [],
      emoji: "🚨",
    },
  ]

  let trashColumn = boards.find((e) => e.name != "DESCARTADOS" || e.name != "BACKLOG ANTIGO")
  let data = cards.filter((e) => e.idList != trashColumn.id)

  data.forEach((e) => {
    let labels = e.labels.filter((e) => ["IMPLEMENTAÇÃO", "CORREÇÃO"].indexOf(e.name) <= -1)

    if (factorSearch(e, "IMPLEMENTAÇÃO")) {
      e.priority = e.labels.find((ee) => {
        return ["MÉDIA", "ALTA", "BAIXA"].findIndex((e) => e === ee.name) > -1
      })?.name

      e.labels = labels
      e.members = e.idMembers.map((e) => {
        return members.find((ee) => ee.idMember === e)
      })
      e.dateDeadLine = extractDeadLine(e.desc)
      e.createdAt = extractCreatedAt(e.desc)
      if (e.dateDeadLine && e.createdAt) {
        group[0].data.push(e)
      }
    } else if (factorSearch(e, "CORREÇÃO")) {
      e.labels = labels
      e.members = e.idMembers.map((e) => {
        return members.find((ee) => ee.idMember === e)
      })
      e.dateDeadLine = extractDeadLine(e.desc)
      e.createdAt = extractCreatedAt(e.desc)
      e.priority = e.labels.find((ee) => {
        return ["MÉDIA", "ALTA", "BAIXA"].findIndex((e) => e === ee.name) > -1
      })?.name
      if (e.dateDeadLine && e.createdAt) {
        group[1].data.push(e)
      }
    }
  })
  return { data: group }
}

export default function Index() {
  let { data } = useLoaderData()
  const currentDate = new Date()
  const nextMonth = new Date(currentDate.setMonth(currentDate.getMonth() + 1))
  return (
    <div
      style={{
        flexDirection: "row",
        display: "flex",
        height: "100%",
      }}
    >
      <LeftNavbar />
      <Box
        flexDirection="column"
        style={{
          padding: "30px",
          marginLeft: "80px",
        }}
      >
        <Title
          style={{
            marginBottom: "24px",
          }}
          title={
            <>
              Backlog Desenvolvimento
              <span style={{ color: "#EBEBED" }}> • </span>
              <span
                style={{
                  color: "#7F7EFF",
                  textTransform: "uppercase",
                }}
              >
                {new Intl.DateTimeFormat("pt-BR", {
                  month: "long",
                }).format(new Date())}
                /
                {new Intl.DateTimeFormat("pt-BR", {
                  month: "long",
                }).format(nextMonth)}
              </span>
            </>
          }
        ></Title>
        <Box flexDirection="row">
          {data?.map((e) => {
            return (
              <ColumnCards
                data={e}
                key={e.name}
                style={{
                  marginRight: "30px",
                  alignSelf: "baseline",
                }}
              />
            )
          })}
        </Box>
      </Box>
    </div>
  )
}
